@import "../../vars.scss";

.photos {
  margin: 0 auto;

  &__heading {
    font-size: 60px;
    margin: 0 0 30px;
  }

  &__text {
    font-family: $font-secondary;
    letter-spacing: 1.25px;
    font-size: 16px;
    line-height: 25px;
    margin: 20px auto;
    text-align: left;

    @media (min-width: 600px) {
      max-width: 700px;
    }
  }

  &__hashtag {
    font-family: $font-secondary;
    letter-spacing: 2px;
    font-size: 30px;
    text-decoration: underline;
    text-underline-offset: 10px;
  }

  &__photo-section {
    margin-top: 90px;
  }
}
